@import "reset";
//@import "mixin";

//$color: unquote('#' + random(999999));
$color: random(360);
//$progresion: adjust-hue($color, 60deg);

:root {
  --font-family: 'Montserrat', sans-serif;
  --regular: 400;
  --bold: 700;
  
  --primary-color: #ff0066;
  --secondary-color: #333333;
  --background-color: #fafafa;

  --code-text-color: #f8f8f8;
  --code-background-color: #424242;
}

body {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  min-height: 100vh;
  font-family: var(--font-family);
  color: var(--secondary-color);
}

body * {
  box-sizing: inherit;
}

.container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 50px;
  margin: 0 auto;
}
.box {
  width: 100px;
  height: 100px;
}

@for $i from 1 through 10 {
.box:nth-child(#{$i}) {
    background-color: hsl($color, ($i * 10%), ($i * 10%));
  }
}

